import React, { useState, useEffect } from 'react';
import { EditorState, convertToRaw, convertFromRaw } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

function Step9() {
    const [scenes, setScenes] = useState([]);

    useEffect(() => {
        const savedScenes = localStorage.getItem('step9Scenes');
        if (savedScenes) {
            const parsedScenes = JSON.parse(savedScenes);
            const loadedScenes = parsedScenes.map(scene => ({
                ...scene,
                editorState: EditorState.createWithContent(convertFromRaw(scene.content))
            }));
            setScenes(loadedScenes);
        }
    }, []);

    const handleEditorChange = (index, state) => {
        const newScenes = [...scenes];
        newScenes[index].editorState = state;
        setScenes(newScenes);
    };

    const handleTitleChange = (index, title) => {
        const newScenes = [...scenes];
        newScenes[index].title = title;
        setScenes(newScenes);
    };

    const handleSave = () => {
        const contentToSave = scenes.map(scene => ({
            title: scene.title,
            content: convertToRaw(scene.editorState.getCurrentContent())
        }));
        localStorage.setItem('step9Scenes', JSON.stringify(contentToSave));
        alert('Content saved!');
    };

    const addScene = () => {
        setScenes([...scenes, { title: '', editorState: EditorState.createEmpty() }]);
    };

    return (
        <div>
            <h2>Step 9: Scene Descriptions</h2>
            {scenes.map((scene, index) => (
                <div key={index}>
                    <input
                        type="text"
                        value={scene.title}
                        onChange={(e) => handleTitleChange(index, e.target.value)}
                        placeholder="Scene Title"
                    />
                    <Editor
                        editorState={scene.editorState}
                        onEditorStateChange={(state) => handleEditorChange(index, state)}
                        toolbar={{
                            options: ['inline', 'blockType', 'fontSize', 'fontFamily', 'list', 'textAlign', 'colorPicker', 'link', 'embedded', 'emoji', 'image', 'remove', 'history'],
                            inline: { inDropdown: true },
                            list: { inDropdown: true },
                            textAlign: { inDropdown: true },
                            link: { inDropdown: true },
                        }}
                        spellCheck={true}
                    />
                </div>
            ))}
            <button onClick={addScene} style={{ marginTop: '20px' }}>Add Scene</button>
            <button onClick={handleSave} style={{ marginTop: '20px' }}>Save All Scenes</button>
        </div>
    );
}

export default Step9;
