import React, { useState, useEffect } from 'react';
import { EditorState, convertToRaw, convertFromRaw } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

function ChapterForm() {
    const [chapters, setChapters] = useState([]);

    useEffect(() => {
        const savedChapters = localStorage.getItem('chapterDescriptions');
        if (savedChapters) {
            const parsedChapters = JSON.parse(savedChapters);
            const loadedChapters = parsedChapters.map(chap => ({
                ...chap,
                editorState: EditorState.createWithContent(convertFromRaw(chap.content)),
                isOpen: false  // Add isOpen property to manage collapsibility
            }));
            setChapters(loadedChapters);
        }
    }, []);

    const toggleCollapse = (index) => {
        const newChapters = chapters.map((chap, idx) => ({
            ...chap,
            isOpen: idx === index ? !chap.isOpen : chap.isOpen
        }));
        setChapters(newChapters);
    };

    const handleEditorChange = (index, state) => {
        const newChapters = [...chapters];
        newChapters[index].editorState = state;
        setChapters(newChapters);
    };

    const handleTitleChange = (index, title) => {
        const newChapters = [...chapters];
        newChapters[index].title = title;
        setChapters(newChapters);
    };

    const handleSave = () => {
        const contentToSave = chapters.map(chap => ({
            title: chap.title,
            content: convertToRaw(chap.editorState.getCurrentContent()),
            isOpen: chap.isOpen
        }));
        localStorage.setItem('chapterDescriptions', JSON.stringify(contentToSave));
        alert('Chapter descriptions saved!');
    };

    const addChapter = () => {
        setChapters([...chapters, { title: '', editorState: EditorState.createEmpty(), isOpen: true }]);
    };

    return (
        <div>
            <h2>Chapter Descriptions</h2>
            {chapters.map((chapter, index) => (
                <div key={index}>
                    <div onClick={() => toggleCollapse(index)} style={{ cursor: 'pointer' }}>
                        <h4>{chapter.title || "Untitled Chapter"}</h4>
                    </div>
                    {chapter.isOpen && (
                        <div>
                            <input
                                type="text"
                                value={chapter.title}
                                onChange={(e) => handleTitleChange(index, e.target.value)}
                                placeholder="Chapter Title"
                            />
                            <Editor
                                editorState={chapter.editorState}
                                onEditorStateChange={(state) => handleEditorChange(index, state)}
                                toolbar={{
                                    options: ['inline', 'blockType', 'fontSize', 'fontFamily', 'list', 'textAlign', 'colorPicker', 'link', 'embedded', 'emoji', 'image', 'remove', 'history'],
                                    inline: { inDropdown: true },
                                    list: { inDropdown: true },
                                    textAlign: { inDropdown: true },
                                    link: { inDropdown: true },
                                }}
                                spellCheck={true}
                            />
                        </div>
                    )}
                </div>
            ))}
            <button onClick={addChapter} style={{ marginTop: '20px' }}>Add Chapter</button>
            <button onClick={handleSave} style={{ marginTop: '20px' }}>Save All Chapters</button>
        </div>
    );
}

export default ChapterForm;
