// src/components/Snowflake/Step1.js
import React, { useState, useEffect } from 'react';

function Step1({ summary, updateSummary }) {
    const [sentence, setSentence] = useState(summary || '');

    useEffect(() => {
        setSentence(summary || '');
    }, [summary]);

    const handleSave = () => {
        updateSummary(sentence);
    };

    return (
        <div>
            <h2>Step 1: One-Sentence Summary</h2>
            <input
                type="text"
                value={sentence}
                placeholder="Enter your story's one-sentence summary"
                onChange={(e) => setSentence(e.target.value)}
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step1;
