import React, { useState, useEffect } from 'react';
import Step1 from './Step1';  // Importing Step 1
import Step2 from './Step2';  // Importing Step 2
import Step3 from './Step3';  // Importing Step 3
import Step4 from './Step4';  // Importing Step 4
import Step5 from './Step5';  // Importing Step 5
import Step6 from './Step6';  // Importing Step 6
import Step7 from './Step7';  // Importing Step 7
import Step8 from './Step8';  // Importing Step 8
import Step9 from './Step9';  // Importing Step 9
import Step10 from './Step10'; // Importing Step 10

function SnowflakeNavigator({ data, updateData }) {
    console.log("Current Novel Data:", data);

    const [currentStep, setCurrentStep] = useState(1);

    const goToStep = (step) => setCurrentStep(step);

    const updateStepData = (step, newData) => {
        const updatedData = { ...data, [step]: newData };
        updateData(updatedData);
    };

    

    const steps = [
        <Step1 summary={data?.step1} updateSummary={(value) => updateStepData('step1', value)} />,
        <Step2 paragraph={data?.step2} updateParagraph={(value) => updateStepData('step2', value)} />,
        <Step3 characterSummaries={data?.step3} updateCharacterSummaries={(value) => updateStepData('step3', value)} />,
        <Step4 expandedSummary={data?.step4} updateExpandedSummary={(value) => updateStepData('step4', value)} />,
        <Step5 detailedCharacterSynopses={data?.step5} updateDetailedCharacterSynopses={(value) => updateStepData('step5', value)} />,
        <Step6 expandedSynopsis={data?.step6} updateExpandedSynopsis={(value) => updateStepData('step6', value)} />,
        <Step7 characterCharts={data?.step7} updateCharacterCharts={(value) => updateStepData('step7', value)} />,
        <Step8 sceneList={data?.step8} updateSceneList={(value) => updateStepData('step8', value)} />,
        <Step9 sceneDescriptions={data?.step9} updateSceneDescriptions={(value) => updateStepData('step9', value)} />,
        <Step10 firstDraft={data?.step10} updateFirstDraft={(value) => updateStepData('step10', value)} />
    ];

    return (
        <div>
            <nav>
                <ul>
                    {Array.from({ length: 10 }, (_, i) => (
                        <li key={i} onClick={() => goToStep(i + 1)}>Step {i + 1}</li>
                    ))}
                </ul>
            </nav>
            <div>{steps[currentStep - 1]}</div>
        </div>
    );
}

export default SnowflakeNavigator;
