import React from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { useChapters } from './ChapterContext';

function MappingArea() {
    const { chapters, setChapters } = useChapters();

    const onDragEnd = (result) => {
        const { source, destination } = result;

        // Dropped outside the list
        if (!destination) {
            return;
        }

        if (source.droppableId === destination.droppableId && source.index !== destination.index) {
            const newChapters = Array.from(chapters);
            const [removed] = newChapters.splice(source.index, 1);
            newChapters.splice(destination.index, 0, removed);
            setChapters(newChapters);
        }
    };

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="chapters">
                {(provided) => (
                    <div
                        {...provided.droppableProps}
                        ref={provided.innerRef}
                        className="mapping-area"
                    >
                        {chapters.map((chapter, index) => (
                            <Draggable key={chapter.id} draggableId={chapter.id.toString()} index={index}>
                                {(provided) => (
                                    <div
                                        ref={provided.innerRef}
                                        {...provided.draggableProps}
                                        {...provided.dragHandleProps}
                                    >
                                        <h4>{chapter.title}</h4>
                                        {/* Display scenes or additional details here */}
                                    </div>
                                )}
                            </Draggable>
                        ))}
                        {provided.placeholder}
                    </div>
                )}
            </Droppable>
        </DragDropContext>
    );
}

export default MappingArea;
