// src/components/Snowflake/Step2.js
import React, { useState, useEffect } from 'react';

function Step2({ paragraph, updateParagraph }) {
    const [summaryParagraph, setSummaryParagraph] = useState(paragraph || '');

    useEffect(() => {
        setSummaryParagraph(paragraph || '');
    }, [paragraph]);

    const handleSave = () => {
        updateParagraph(summaryParagraph);
    };

    return (
        <div>
            <h2>Step 2: Expand to a Paragraph</h2>
            <textarea
                value={summaryParagraph}
                placeholder="Expand your sentence into a full paragraph"
                onChange={(e) => setSummaryParagraph(e.target.value)}
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step2;
