import React from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { useChapters } from './ChapterContext';

function MappingTool() {
    const { chapters, updateCharacterInChapter } = useChapters();

    const onDragEnd = (result) => {
        const { source, destination } = result;
        if (!destination) {
            return;
        }

        // Assume characters are draggable and chapters/scenes are droppable
        if (source.droppableId !== destination.droppableId) {
            updateCharacterInChapter(source.index, destination.droppableId);
        }
    };

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            {/* Draggable items for characters might be set up here */}
            {chapters.map((chapter, index) => (
                <Droppable key={chapter.id} droppableId={`chapter-${chapter.id}`}>
                    {(provided) => (
                        <div ref={provided.innerRef} {...provided.droppableProps}>
                            <h3>{chapter.title}</h3>
                            {/* Assuming you're dropping characters into chapters */}
                            {provided.placeholder}
                        </div>
                    )}
                </Droppable>
            ))}
        </DragDropContext>
    );
}

export default MappingTool;
