// src/components/Snowflake/Step4.js
import React, { useState, useEffect } from 'react';

function Step4({ expandedSummary, updateExpandedSummary }) {
    const [expanded, setExpanded] = useState(expandedSummary || '');

    useEffect(() => {
        setExpanded(expandedSummary || '');
    }, [expandedSummary]);

    const handleSave = () => {
        updateExpandedSummary(expanded);
    };

    return (
        <div>
            <h2>Step 4: Expand Each Sentence to a Paragraph</h2>
            <textarea
                value={expanded}
                placeholder="Expand each sentence of your summary into a paragraph"
                onChange={(e) => setExpanded(e.target.value)}
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step4;
