// src/components/Snowflake/Step3.js
import React, { useState, useEffect } from 'react';

function Step3({ characterSummaries, updateCharacterSummaries }) {
    const [summaries, setSummaries] = useState(characterSummaries || '');

    useEffect(() => {
        setSummaries(characterSummaries || '');
    }, [characterSummaries]);

    const handleSave = () => {
        updateCharacterSummaries(summaries);
    };

    return (
        <div>
            <h2>Step 3: Character Summaries</h2>
            <textarea
                value={summaries}
                placeholder="Write detailed summaries for each main character"
                onChange={(e) => setSummaries(e.target.value)}
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step3;
