import React, { createContext, useContext, useState } from 'react';

const ChapterContext = createContext();

export const useChapters = () => useContext(ChapterContext);

export const ChapterProvider = ({ children }) => {
    const [chapters, setChapters] = useState([]);

    const addChapter = (chapter) => {
        const updatedChapters = [...chapters, chapter];
        setChapters(updatedChapters);
        localStorage.setItem('chapters', JSON.stringify(updatedChapters));
    };

    const loadChapters = () => {
        const storedChapters = JSON.parse(localStorage.getItem('chapters')) || [];
        setChapters(storedChapters);
    };

    return (
        <ChapterContext.Provider value={{ chapters, setChapters, addChapter, loadChapters }}>
            {children}
        </ChapterContext.Provider>
    );
};
