import React, { useState, useEffect } from 'react';
import { EditorState, convertToRaw, convertFromRaw } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

function CharacterForm() {
    const [characters, setCharacters] = useState([]);

    useEffect(() => {
        const savedCharacters = localStorage.getItem('characterDescriptions');
        if (savedCharacters) {
            const parsedCharacters = JSON.parse(savedCharacters);
            const loadedCharacters = parsedCharacters.map(character => ({
                ...character,
                editorState: EditorState.createWithContent(convertFromRaw(character.content)),
                isOpen: false  // Add isOpen property to manage collapsibility
            }));
            setCharacters(loadedCharacters);
        }
    }, []);

    const toggleCollapse = (index) => {
        const newCharacters = characters.map((character, idx) => ({
            ...character,
            isOpen: idx === index ? !character.isOpen : character.isOpen
        }));
        setCharacters(newCharacters);
    };

    const handleEditorChange = (index, state) => {
        const newCharacters = [...characters];
        newCharacters[index].editorState = state;
        setCharacters(newCharacters);
    };

    const handleTitleChange = (index, title) => {
        const newCharacters = [...characters];
        newCharacters[index].title = title;
        setCharacters(newCharacters);
    };

    const handleSave = () => {
        const contentToSave = characters.map(character => ({
            title: character.title,
            content: convertToRaw(character.editorState.getCurrentContent()),
            isOpen: character.isOpen
        }));
        localStorage.setItem('characterDescriptions', JSON.stringify(contentToSave));
        alert('Character descriptions saved!');
    };

    const addCharacter = () => {
        setCharacters([...characters, { title: '', editorState: EditorState.createEmpty(), isOpen: true }]);
    };

    return (
        <div>
            <h2>Character Descriptions</h2>
            {characters.map((character, index) => (
                <div key={index}>
                    <div onClick={() => toggleCollapse(index)} style={{ cursor: 'pointer' }}>
                        <h4>{character.title || "Untitled Character"}</h4>
                    </div>
                    {character.isOpen && (
                        <div>
                            <input
                                type="text"
                                value={character.title}
                                onChange={(e) => handleTitleChange(index, e.target.value)}
                                placeholder="Character Name"
                            />
                            <Editor
                                editorState={character.editorState}
                                onEditorStateChange={(state) => handleEditorChange(index, state)}
                                toolbar={{
                                    options: ['inline', 'blockType', 'fontSize', 'fontFamily', 'list', 'textAlign', 'colorPicker', 'link', 'embedded', 'emoji', 'image', 'remove', 'history'],
                                    inline: { inDropdown: true },
                                    list: { inDropdown: true },
                                    textAlign: { inDropdown: true },
                                    link: { inDropdown: true },
                                }}
                                spellCheck={true}
                            />
                        </div>
                    )}
                </div>
            ))}
            <button onClick={addCharacter} style={{ marginTop: '20px' }}>Add Character</button>
            <button onClick={handleSave} style={{ marginTop: '20px' }}>Save All Characters</button>
        </div>
    );
}

export default CharacterForm;

