// src/components/Snowflake/Step6.js
import React, { useState, useEffect } from 'react';

function Step6({ expandedPlotSynopsis, updateExpandedPlotSynopsis }) {
    const [expanded, setExpanded] = useState(expandedPlotSynopsis || '');

    useEffect(() => {
        setExpanded(expandedPlotSynopsis || '');
    }, [expandedPlotSynopsis]);

    const handleSave = () => {
        updateExpandedPlotSynopsis(expanded);
    };

    return (
        <div>
            <h2>Step 6: Expand Your Plot Synopsis</h2>
            <textarea
                value={expanded}
                onChange={(e) => setExpanded(e.target.value)}
                placeholder="Expand your one-page plot synopsis into a four-page synopsis"
                rows="10"
                cols="50"
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step6;
