// src/components/Snowflake/Step7.js
import React, { useState, useEffect } from 'react';

function Step7({ characterCharts, updateCharacterCharts }) {
    const [charts, setCharts] = useState(characterCharts || '');

    useEffect(() => {
        setCharts(characterCharts || '');
    }, [characterCharts]);

    const handleSave = () => {
        updateCharacterCharts(charts);
    };

    return (
        <div>
            <h2>Step 7: Character Charts</h2>
            <textarea
                value={charts}
                onChange={(e) => setCharts(e.target.value)}
                placeholder="Create detailed charts for each character"
                rows="10"
                cols="50"
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step7;

