// src/components/Snowflake/Step5.js
import React, { useState, useEffect } from 'react';
import { EditorState, convertToRaw, convertFromRaw } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

function Step5({ detailedCharacterSynopses, updateDetailedCharacterSynopses }) {
  const [editorState, setEditorState] = useState(EditorState.createEmpty());
    useEffect(() => {
        const savedContent = localStorage.getItem('step5Content');
        if (savedContent) {
            const contentState = convertFromRaw(JSON.parse(savedContent));
            setEditorState(EditorState.createWithContent(contentState));
        }
    }, []);

    const handleEditorChange = (state) => {
        setEditorState(state);
    };

    const handleSave = () => {
        const contentState = editorState.getCurrentContent();
        const contentRaw = convertToRaw(contentState);
        localStorage.setItem('step5Content', JSON.stringify(contentRaw));
        alert('Content saved!');
    };

    return (
        <div>
            <h2>Step 5: Detailed Character Synopses</h2>
            <Editor
                editorState={editorState}
                onEditorStateChange={handleEditorChange}
                toolbar={{
                    options: ['inline', 'blockType', 'fontSize', 'fontFamily', 'list', 'textAlign', 'colorPicker', 'link', 'embedded', 'emoji', 'image', 'remove', 'history'],
                    inline: { inDropdown: true },
                    list: { inDropdown: true },
                    textAlign: { inDropdown: true },
                    link: { inDropdown: true },
                }}
                spellCheck={true}
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step5;
