// src/components/Snowflake/Step8.js
import React, { useState, useEffect } from 'react';

function Step8({ sceneList, updateSceneList }) {
    const [list, setList] = useState(sceneList || '');

    useEffect(() => {
        setList(sceneList || '');
    }, [sceneList]);

    const handleSave = () => {
        updateSceneList(list);
    };

    return (
        <div>
            <h2>Step 8: Scene List</h2>
            <textarea
                value={list}
                onChange={(e) => setList(e.target.value)}
                placeholder="List all scenes with brief descriptions"
                rows="10"
                cols="50"
            />
            <button onClick={handleSave}>Save</button>
        </div>
    );
}

export default Step8;

